.color-box {
    width: 50px;
    height: 50px;
    -webkit-transition: -webkit-transform 150ms;
    transition: -webkit-transform 150ms;
    -o-transition: transform 150ms;
    transition: transform 150ms;
    transition: transform 150ms, -webkit-transform 150ms;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 0 50px 3px rgba(0, 0, 0, 0.1);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 0 50px 3px rgba(0, 0, 0, 0.1);
    border: 4px solid #f1f1f1;
}

.color-box:hover {
    -webkit-transform: scale(1.4, 1.4) rotate(90deg);
        -ms-transform: scale(1.4, 1.4) rotate(90deg);
            transform: scale(1.4, 1.4) rotate(90deg);
}


